import React, {Fragment} from 'react';
import './style.scss';
import TabsManager from './../../TabsManager/TabsManager';
import MessageTable from './MessageTable/MessageTable';
import {connect} from "react-redux";
import { FormattedMessage } from 'react-intl';

const Tickets = (props) => {

    let currentComponent;

    let tabs = [
        { name : (<FormattedMessage id='tabsManager.menu.tickets'/>), link: '/tickets'},
        { name : (<FormattedMessage id='tabsManager.menu.newTicket'/>), link: '/new-ticket'},
    ];

    let ticketsComponent = (
        <Fragment>
            <TabsManager data={tabs} pageTitle={'Tickets'}/>
            <div className="dashboard-content">

                <MessageTable />
            </div>
        </Fragment>
    );

    return ticketsComponent;


};

const mapStateToProps = (state) => ({
    tickets: state.tickets.tickets,
    appState: state.appOptions.appState
});

export default connect(mapStateToProps)(Tickets);