import React from 'react';
import {searchData} from "../../../../redux/config-purchased/config.actions";
import {connect} from "react-redux";
import { FormattedMessage } from 'react-intl';

const ConfigAlert = (props) => {


        let summ = null;
        let counter = 0;

        {props.configPurchased.selectedData.map((i, key) => {
            if(i) {
                counter++;
                summ = summ + i.price
            }

        })}

        if(summ) {
            return (<div className="table_action_bar">
                <div className="btn btn--transparent btn--cancel"><FormattedMessage  id='button.cancel' /></div>
                <div>
                <div className="table_action_bar__info">{counter} <FormattedMessage  id='alertPanel.selectedFor' /> <b>${summ}</b></div>
                <div className="btn btn--blue"><FormattedMessage  id='button.useSelected' /></div>
                </div>
                </div>);
        }else {
            return null;
        }

};


const mapStateToProps = (state) => ({
    configPurchased: state.configPurchased.configShop,
});

const mapDispatchToProps = (dispatch) => {
    return {
        searchData: (prop) => dispatch(searchData(prop)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ConfigAlert);