import React, {useEffect} from 'react';

import {connect} from "react-redux";
import FormControl from "@material-ui/core/FormControl";
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";
import {
    setAccessRightsModal,
    setAuthorizationDataModal,
    setLicenseDaysSettingsModal
} from "../../../../redux/app/app.actions";
import {updateUsers} from "../../../../redux/licenses/corporate/users/users.actions";
import { FormattedMessage } from 'react-intl';

const LicenseAlert = (props) => {
    const [selectValue, setSelectValue] = React.useState('Get authorization data');
    const [showBar, setShowBar] = React.useState(false);
    const [counter, setCounter] = React.useState(0);

    let summ = null;
    let mapCounter = 0;

    let dataArray = [];
    let selectedDataArray = [];

    props.usersTabData.map((i, key) => {
        if (i.userOptions.isSelected) {
            selectedDataArray.push(i);
        }
    });


    const applyHandler = () => {

        switch (selectValue) {
            case 'Get authorization data':
                props.setAuthorizationDataModal(true);
                break

            case 'Set access rights':
                props.setAccessRightsModal({bool: true,data: null});
                break
            case 'Delete selected data':
                props.usersTabData.map((i, key) => {
                    if (!i.userOptions.isSelected) {
                        dataArray.push(i);
                    }
                });

                props.updateUsers(dataArray);
                break
            default:
                break
        }
    };


    const cancelHandler = () => {
        let data = props.usersTabData.map((i, key) => {
            i.userOptions.isSelected = false;
            return i;
        });

        props.updateUsers(data);

    };


    const barComponent = (<div className="table_action_bar table_action_bar--license table_action_bar--mobile">
        <FormControl className="table_action_bar__select">
            <TextField
                value={selectValue}
                onChange={(e) => {
                    setSelectValue(e.target.value)
                }}
                fullWidth
                select
                variant="filled"
            >
                <MenuItem key={1} value={'Get authorization data'}>Get authorization data</MenuItem>
                <MenuItem key={2} value={'Set access rights'}>Set access rights</MenuItem>
                <MenuItem key={3} value={'Delete selected data'}>Delete selected data</MenuItem>
            </TextField>
        </FormControl>
        <div>
            <Button variant="contained" className="btn btn--transparent btn--cancel" onClick={() => {
                cancelHandler()
            }}>
                <FormattedMessage  id='button.cancel'/>
            </Button>

        </div>
        <p className="table_action_bar__info">{selectedDataArray.length} <FormattedMessage  id='alertPanel.usersSelected'/></p>
        <div>

            <Button variant="contained" className="btn btn--blue" onClick={() => {
                applyHandler()
            }}>
                <FormattedMessage  id='button.apply'/>
            </Button>
        </div>
    </div>);


    return selectedDataArray.length > 0 ? barComponent : null;

};


const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setAccessRightsModal: (bool) => dispatch(setAccessRightsModal(bool)),
        setAuthorizationDataModal: (bool) => dispatch(setAuthorizationDataModal(bool)),
        updateUsers: (obj) => dispatch(updateUsers(obj)),
        setLicenseDaysSettingsModal: (obj) => dispatch(setLicenseDaysSettingsModal(obj)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LicenseAlert);

