import React, {Fragment} from 'react';
import {connect} from "react-redux";

import {getDate} from "../../../../redux/Utils";
import {IconPack} from "../../../../Images/IconPack";
import { FormattedMessage } from 'react-intl';

import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Button from "@material-ui/core/Button";
import {setLicenseDaysSettingsModal, setChangeDealModal} from "../../../../redux/app/app.actions";

const DealsAccordion = (props) => {

    const [searchValue, setSearchValue] = React.useState('');
    const [dateReverse, setDateReverse] = React.useState(false);
    const [expanded, setExpanded] = React.useState(false);

    const [editState, setEditState] = React.useState(false);

    const buttonHandler = (data) => {
        // props.setChangeDealModal({bool:true, data:data})

        setEditState(true);
    }

    const getAccordions = (reversed) => {

        if(!reversed){
            return props.deals.map((i, key) => {
                    return  getAccordionItem(i, key);
                });
        }else {
            return props.deals.map((i, key) => {
                    return  getAccordionItem(i, key);
                }).reverse()
        }
    };

    const handleAccordionsChange = (panel) => (event, isExpanded) => {

        setExpanded(isExpanded ? panel : false);
    };

    const getAccordionContent = (item) => {
        return (
            <> <span className="accordion-data-description">Status</span>
                {item.status}
                <span className="accordion-data-description">Seller</span>
                <span className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}{item.seller}</span>

                <span className="accordion-data-description">Buyer</span>
                <span className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}{item.buyer}</span>


                <span className="accordion-data-description">Amount</span>
                {item.amount}

                <span className="accordion-data-description">Deal Id</span>
                {item.dealId}

                <span className="accordion-data-description">Date created</span>
                {item.dateCreated}

                <Button variant="contained" className="btn btn--blue mb-15 mt-10" onClick={()=>{buttonHandler(item)}}>
                    Details
                </Button>
            </>
        );
    }




    const getAccordionItem = (item, key) => {
        return (
            <Accordion  expanded={expanded === 'panel'+key} onChange={handleAccordionsChange('panel'+key)}>
                <AccordionSummary
                    aria-controls={"panel"+key+"a-content"}
                    id={"panel"+key+"a-header"}
                >
                    {item.title}
                </AccordionSummary>
                <AccordionDetails>

                    {editState ? null : getAccordionContent(item)}


                </AccordionDetails>
            </Accordion>
       )
    };


    return (<div className="ustomStyle">  <div className='tabs_wrapper'>
        <span className="mb-10 tabsContentHeader">Deal title</span>
        {getAccordions(dateReverse)}
    </div></div>);
}




const mapStateToProps = (state) => ({
    deals: state.deals.deals,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLicenseDaysSettingsModal: (type) => dispatch(setLicenseDaysSettingsModal(type)),
        setChangeDealModal: (type) => dispatch(setChangeDealModal(type)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DealsAccordion);