import React, {Fragment} from 'react';
import TabsManager from './../../TabsManager/TabsManager';
import TransactionsTable from './TransactionsTable/TransactionsTable';
import { FormattedMessage } from 'react-intl';


const Transactions = () => {

    let tabs = [
        {name: (<FormattedMessage id='tabsManager.menu.operations'/>), link: '/operations'},
        {name: (<FormattedMessage id='tabsManager.menu.transactions'/>), link: '/transactions'}
    ];


    return (
        <Fragment>
                <TabsManager data={tabs} pageTitle={'Transactions'}/>
                <div className="dashboard-content">
                    <TransactionsTable />
                </div>
        </Fragment>
    );
};

export default Transactions;



