import React, {Fragment} from 'react';
import TabsManager from './../../TabsManager/TabsManager';
import OperationsAccordions from './OperationsAccordions/OperationsAccordions';
import { FormattedMessage } from 'react-intl';

const Operations = () => {

    let tabs = [
        {name : (<FormattedMessage id='tabsManager.menu.operations'/>), link: '/operations'},
        {name : (<FormattedMessage id='tabsManager.menu.transactions'/>), link: '/transactions'}
    ];


    return (
        <Fragment>
                <TabsManager data={tabs} pageTitle={'Operations'}/>
                <div className="dashboard-content">
                    <OperationsAccordions />
                </div>
        </Fragment>
    );
};

export default Operations;