import React from 'react';
import Icon from './user.icon.svg';

const Message = (props) => {

    let size;
    let messageItemClassName = 'messages__item';

    if(props.data.message.length < 30 ){
        size = ' messages__item--small';
    }else if(props.data.message.length < 150){
        size = ' messages__item--medium';
    }else if(props.data.message.length > 150){
        size = ' messages__item--large';
    }

    messageItemClassName = messageItemClassName + size;

    if(props.data.owner) {
        messageItemClassName = messageItemClassName + ' messages__item--owner';
    }

    return (
        <div className={messageItemClassName}>
            <header>
                <div className="profile">
                    <img src={Icon} alt="Icon"/>
                    {props.data.author}</div>
                <div className="date">{props.data.date}</div>
            </header>
            <main>
                {props.data.message}
            </main>
        </div>
    );
};

export default Message;