import React, { useState, useEffect } from 'react';
import MaterialTable from 'material-table';
import ConfigLimits from "../../../TableComponents/ConfigLimits/ConfigLimits";
import DaysLeft from "../../../TableComponents/DaysLeft/DaysLeft";
import {connect} from "react-redux";
import Switch from '@material-ui/core/Switch';

import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { setDeviceFilter, setOsFilter, setOsVersionFilter, setBrowserFilter, setBrowserVersionFilter, updateData, selectItem, searchData } from "../../../../redux/config-shop/config.actions";
import TextField from "@material-ui/core/TextField/TextField";
import { IconPack } from "../../../../Images/IconPack";
import Checkbox from '@material-ui/core/Checkbox';
import { FormattedMessage } from 'react-intl';


import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails/AccordionDetails";
import Accordion from "@material-ui/core/Accordion/Accordion";
import Button from "@material-ui/core/Button";

const Purchased = (props) => {

    useEffect(() => {
        props.setDeviceFilter('Desktop');
        props.setOsFilter('Windows');
        props.updateData();
    }, []);
    const [searchValue, setSearch] = React.useState('');

    const [isSearchActive, setSearchActive] = React.useState(false);
    const [isCheckboxesActive, setCheckboxesActive] = React.useState(true);

    const searchEventHandler = (e) => {

        if(e.target.value != 0) {
            setSearch(e.target.value);
            props.searchData(e.target.value);
            setSearchActive(true)
        }else {
            setSearch(e.target.value);
            setSearchActive(false)
        }


    }

    const deviceEventHandler = (e) => {
        props.setDeviceFilter(e.target.value);
        props.updateData();
    }
    const osEventHandler = (e) => {
        props.setOsFilter(e.target.value);

        let currentIndex = props.configPurchased.sort.os.find((obj)=> {
                return obj.title === e.target.value
        });
        props.setOsVersionFilter(currentIndex.version[0].toString());
        props.updateData();
    }
    const osVersionsEventHandler = (e) => {
        props.setOsVersionFilter(e.target.value);
        props.updateData();
    }

    const browserEventHandler = (e) => {
        props.setBrowserFilter(e.target.value);

        let currentIndex = props.configPurchased.sort.browser.find((obj)=> {
            return obj.title === e.target.value
        });

        props.setBrowserVersionFilter(currentIndex.version[0].toString());
        props.updateData();
    }
    const browserVersionsEventHandler = (e) => {
        props.setBrowserVersionFilter(e.target.value);
        props.updateData();
    }

    const buyEventHandler = (dataObj) => {

        setCheckboxesActive(true)
    }
    const checkBoxHandler = (data, value) => {

        props.selectItem(data);
    }

    const [configurator, setConfigurator] = React.useState(false);

    const [expanded, setExpanded] = React.useState();

    const handleAccordion = (panel, e) => {

        if (e.target.localName !== 'input') {
            setExpanded(panel === expanded ? null : panel);
        }

    };

    const contentHtml = (row, key) => {
        return <Accordion expanded={expanded === 'panel' + key}>
            <AccordionSummary
                aria-controls={"panel" + key + "b-content"}
                id={"panel" + key + "b-header"}
                onClick={(e) => {
                    handleAccordion('panel' + key, e)
                }}
                className={row.isSelected ? 'activeHeader' : null}
            >

                <Checkbox color="primary" onClick={()=>{checkBoxHandler(row); buyEventHandler(row)}}  checked={row.isSelected} />
                <div className="shopObject color-white shopObject--mobile">
                    <div className="mr-10">{row.deviceObject.icon}{row.deviceObject.title}</div>
                    <div className="mr-10">{row.osObject.icon}{row.osObject.version}</div>
                    <div className="mr-10">{row.browserObject.icon}{row.browserObject.version}</div>
                </div>

            </AccordionSummary>
            <AccordionDetails>
                <div className="license-users-content">
                    <span className="accordion-data-description">Screen</span>
                    {row.screen}
                    <span className="accordion-data-description">User agent</span>
                    {row.userAgent}
                    <span className="accordion-data-description">Videocard</span>
                    {row.videoCard}
                    <span className="accordion-data-description">Created</span>
                    {row.created}


                    <Button variant="contained" className="btn btn--blue mb-15 mt-10">
                        Buy for ${row.price}
                    </Button>
                </div>

            </AccordionDetails>
        </Accordion>
    };


    console.dir(props.configPurchased);


    let tableSearchContent = (props.configPurchased.searchData.map((row, key) => {
        return contentHtml(row, key);
    }));

    let tableContent = (props.configPurchased.tableData.map((row, key) => {
        return contentHtml(row, key);
    }));




    return (<div className="ustomStyle">
        <div className="filterPanel filterPanel--mobile filterPanel--configurator">


            <Accordion expanded={configurator}>
                <AccordionSummary
                    aria-controls={"panel1a-content"}
                    id={"panel1a-header"}
                    onClick={()=>{setConfigurator(!configurator)}}
                >
                    Configurator
                </AccordionSummary>
                <AccordionDetails className="configurator">


                    <div>
                        <FormControl variant="filled" className='custom_label w100percent mb-10'>
                            <InputLabel id="demo-simple-select-filled-label"><FormattedMessage id='filterPanel.device'/></InputLabel>
                            <Select
                                labelId="demo-simple-select-filled-label"
                                value={props.configPurchased.currentSort.device}
                                onChange={(e) => {
                                    deviceEventHandler(e)
                                }}>
                                {props.configPurchased.sort.device.map((i, key) => {
                                    return (<MenuItem key={key} value={i.title}>{i.icon}{i.title}</MenuItem>);
                                })}
                            </Select>
                        </FormControl>
                    </div>
                    <div className="filterPanel__doubled mb-10">
                        <FormControl variant="filled" className=' custom_label w100percent'>
                            <InputLabel id="demo-simple-select-filled-label"><FormattedMessage id='filterPanel.operationSystem'/></InputLabel>
                            <Select
                                labelId="demo-simple-select-filled-label"
                                value={props.configPurchased.currentSort.os}
                                onChange={(e) => {
                                    osEventHandler(e)
                                }}>


                                {props.configPurchased.sort.os.map((i, key) => {

                                    return (<MenuItem key={key} value={i.title}>{i.icon}{i.title}</MenuItem>);

                                })}

                            </Select>
                        </FormControl>
                        <FormControl variant="filled" className='w150'>
                            <InputLabel id="demo-simple-select-filled-label"><FormattedMessage id='filterPanel.version'/></InputLabel>
                            <Select
                                labelId="demo-simple-select-filled-label"
                                value={props.configPurchased.currentSort.osVersion}
                                onChange={(e) => {
                                    osVersionsEventHandler(e)
                                }}>

                                {props.configPurchased.sort.os.map((i) => {
                                    if (i.title === props.configPurchased.currentSort.os) {
                                        return i.version.map((version, key) => {
                                            return (<MenuItem key={key} value={version}>{version}</MenuItem>);
                                        });
                                    }
                                })}

                            </Select>
                        </FormControl>
                    </div>

                    <div className="filterPanel__doubled mb-10">
                        <FormControl variant="filled" className='w100percent custom_label'>
                            <InputLabel id="demo-simple-select-filled-label"><FormattedMessage id='filterPanel.browser'/></InputLabel>
                            <Select
                                labelId="demo-simple-select-filled-label"
                                value={props.configPurchased.currentSort.browser}
                                onChange={(e) => {
                                    browserEventHandler(e)
                                }}>

                                {props.configPurchased.sort.browser.map((i, key) => {

                                    return (<MenuItem key={key} value={i.title}>{i.icon}{i.title}</MenuItem>);

                                })}

                            </Select>
                        </FormControl>
                        <FormControl variant="filled" className='w150'>
                            <InputLabel id="demo-simple-select-filled-label"><FormattedMessage id='filterPanel.version'/></InputLabel>
                            <Select
                                labelId="demo-simple-select-filled-label"
                                value={props.configPurchased.currentSort.browserVersion}
                                onChange={(e) => {
                                    browserVersionsEventHandler(e)
                                }}>

                                {props.configPurchased.sort.browser.map((i) => {
                                    if (i.title === props.configPurchased.currentSort.browser) {
                                        return i.version.map((version, key) => {
                                            return (<MenuItem key={key} value={version}>{version}</MenuItem>);
                                        });
                                    }
                                })}

                            </Select>
                        </FormControl>
                    </div>


                    <FormattedMessage id='filterPanel.search'>
                        {(msg) => (<TextField
                            variant="filled"
                            fullWidth
                            className="search_area"
                            placeholder={msg}
                            value={searchValue}
                            onChange={(e) => {
                                searchEventHandler(e)
                            }}>
                        </TextField>)}
                    </FormattedMessage>
                    <div className="search_additional_components">
                        <div className="calendar">{IconPack.searchComponents.calendar}</div>

                    </div>

                </AccordionDetails>
            </Accordion>



        </div>
        <div className='tabs_wrapper'>
            <span className="mb-10 tabsContentHeader">Device, Os, Browser</span>
            {isSearchActive ? tableSearchContent : tableContent}
        </div>
        </div>);
}

const mapStateToProps = (state) => ({
    configPurchased: state.configPurchased.configShop,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setDeviceFilter: (prop) => dispatch(setDeviceFilter(prop)),
        updateData: () => dispatch(updateData()),
        setOsFilter: (prop) => dispatch(setOsFilter(prop)),
        setOsVersionFilter: (prop) => dispatch(setOsVersionFilter(prop)),
        setBrowserFilter: (prop) => dispatch(setBrowserFilter(prop)),
        setBrowserVersionFilter: (prop) => dispatch(setBrowserVersionFilter(prop)),
        searchData: (prop) => dispatch(searchData(prop)),
        selectItem: (prop) => dispatch(selectItem(prop)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Purchased);

