import React, {Fragment} from 'react';
import {connect} from "react-redux";
import {setChatState, setChatId} from "../../../../../redux/app/app.actions";
import MessageComponent from "../../Chat/Chat";

import {IconPack} from "../../../../../Images/IconPack";
import { FormattedMessage } from 'react-intl';
import { useHistory } from "react-router-dom";

import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails/AccordionDetails";
import Accordion from "@material-ui/core/Accordion/Accordion";

import Button from "@material-ui/core/Button";


const MessageTable = (props) => {
    let history = useHistory();

    const rowHandler = (data) => {
        props.setChatId(data.chatId);
        props.setChatState(true);
        history.push('tickets/'+data.chatId);
    }

    const messageComponent = (rowData) => {
        return (<Fragment><span className="message-author">{rowData.data[rowData.data.length-1].author}:</span>{rowData.data[rowData.data.length-1].message}</Fragment>);
    };
    const lastMessageDate = (rowData) => {
        return (<Fragment>{rowData.data[rowData.data.length-1].date}</Fragment>);
    };




    const rows = props.tickets.map((item, key) => {
        return (
            <div className="accordion-link" onClick={()=>{rowHandler(item)}}>
                <span>{item.title} </span> <span>{lastMessageDate(item)}</span>
            </div>
        )
    });



    return (
        <div className="ustomStyle">
            <span className="mb-10 tabsContentHeader">Tickets title</span>
           {rows}
        </div>
    );
}
const mapStateToProps = (state) => ({
    tickets: state.tickets.tickets,
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChatState: (bool) => dispatch(setChatState(bool)),
        setChatId: (numb) => dispatch(setChatId(numb)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MessageTable);