import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";
import { FormattedMessage } from 'react-intl';
import Accordion from "@material-ui/core/Accordion/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails/AccordionDetails";

const TransactionsTable = (props) => {
    const [searchValue, setSearchValue] = React.useState('');

    const getDayRow = (type) => {
        let date = new Date();
        var month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
            "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][date.getMonth()];
        let day;
        switch (type) {
            case 'Today':
                day = date.getDate();
                break;
            case 'Yesterday':
                day = date.getDate() - 1;
                break;
            default:
                break;
        }

        return (<div className="day-row">
            <b className="color-white">{type}</b>
            <span className="color-grey inline">{day} {month}</span>
        </div>);
    }


    const getTbodyData = (reversed) => {

            return (<>
                {getDayRow('Today')}
                <div className="color-grey mb-10">USD  /  Status</div>
                {props.balanceData.map((i, key) => {
                    return itemHTML(i, key);
                })}

                {getDayRow('Yesterday')}

                {props.balanceData.map((i, key) => {
                    return itemHTML(i, key);
                })}

            </>);

    };




    const prepearAmount = (amount, type) => {
        switch (type) {
            case 'loss':
                return (
                    <span className="red">{amount}</span>)
                break;
            case 'profit':
                return (<span className="green">{amount}</span>)
                break;
            default:
                return amount
                break;
        }
    }

    const timeConverter = (UNIX_timestamp) => {
        var a = new Date(UNIX_timestamp * 1000);
        var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' '  + ' ' + hour + ':' + min + ':' + sec ;
        return time;
    }


    const [expanded, setExpanded] = React.useState();

    const handleAccordion = (panel, e) => {

        setExpanded(panel === expanded ? null : panel);

    };

    const itemHTML = (i, key) => {
        return  (
            <Accordion expanded={expanded === 'panel' + key}>
                <AccordionSummary
                    aria-controls={"panel"+key+"a-content"}
                    id={"panel"+key+"a-header"}
                    onClick={(e) => {
                        handleAccordion('panel' + key, e)
                    }}
                >
                    <span className="green inline mr-10">{i.amount}$</span><span className="bold align-items-baseline">{IconPack.operations.statusGood} {i.status}</span>
                </AccordionSummary>
                <AccordionDetails>

                    <span className="accordion-data-description">Payment system</span>
                    Bitcoin

                    <span className="accordion-data-description">Description</span>
                    Top up balance

                    <span className="accordion-data-description">Transaction ID</span>
                    <span className="icon-inside icon-inside--right align-items-baseline ">154895327595{IconPack.operations.id}</span>

                    <span className="accordion-data-description">Date</span>
                    {timeConverter(i.date)}
                </AccordionDetails>
            </Accordion>
        );
    }



    let searchedData = props.balanceData.filter(i => {

        if (
            i.status.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.paymentSystem.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.description.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.transactionID.toLowerCase().includes(searchValue.toLowerCase())
        ) {
            return i
        }

    })

    searchedData = searchedData.map((i, key) => {

        return itemHTML(i, key);
    });



    return (
        <div className="ustomStyle">
            <div className="filterPanel  filterPanel--mobile">

                    <FormattedMessage  id='filterPanel.search'>
                        {(msg) => ( <TextField
                            variant="filled"
                            fullWidth
                            className="search_area"
                            placeholder={msg}
                            value={searchValue}
                            //onChange={(e)=>{setSearchValue(e.target.value)}}
                            >
                        </TextField>)}
                    </FormattedMessage>

                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>

                </div>
            </div>
             {searchValue.length ? searchedData : getTbodyData() }
        </div>
    );
}

const mapStateToProps = (state) => ({
    balanceData: state.balance.balanceData.transactions,
});



export default connect(mapStateToProps)(TransactionsTable);






