import React, {Fragment} from 'react';
import './style.scss';
import TabsManager from './../../TabsManager/TabsManager';

import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';

import {connect} from "react-redux";

import { useHistory } from "react-router-dom";
import Switch from '@material-ui/core/Switch';

import { IconPack } from './../../../../Images/IconPack'
import { FormattedMessage } from 'react-intl';

const NewDeal = (props) => {


    let tabs = [
        {name:  (<FormattedMessage id='tabsManager.menu.myDeals'/>), link: '/deals'},
        {name:  (<FormattedMessage id='tabsManager.menu.newDeal'/>), link: '/new-deal'},
    ];

    const [title, setTitle] = React.useState('Questions about buying a license');
    const [status, setStatus] = React.useState('Plan to take over the universe');
    const [creationDate, setCreationDate] = React.useState('');
    const [seller, setSeller] = React.useState('');
    const [buyer, setBuyer] = React.useState(props.currentUser.name);
    const [amountToSend, setAmountToSend] = React.useState(1000);
    const [amountComisionRate, setAmountComisionRate] = React.useState(100);
    const [amountToRecive, setAmountToRecive] = React.useState(1100);
    const [dealId, setDealId] = React.useState('');
    const [dateCreated, setDateCreated] = React.useState('');
    const [isBuyer, setIsBuyer] = React.useState(true);
    const [description, setDescription] = React.useState('');

    const titleHandler = (event) => {
        setTitle(event.target.value);
    };

    const messHandler = (event) => {
        setDescription(event.target.value);
    };

    let history = useHistory();

    const createTicket = () => {
        let currentIndex = Date.now();
        let dealObj = {
                title: title,
                status: 'Completed',
                creationDate: '',
                seller: 'Ronaldo',
                buyer: 'Lionel Messi',
                amountToSend: 1000,
                amountComisionRate: 100,
                amountToRecive: 1100,
                dealId: 1588418078101,
                dateCreated: currentIndex,
                isBuyer:true,
            }

        history.push('/deals');
    }


    let buyerBTN, sellerBTN;

    if (isBuyer) {
        buyerBTN = (<div className="buyer active" onClick={()=>{sellerBuyerSetter(true)}}>{IconPack.checkTrue}I AM A BUYER</div>);
        sellerBTN = (<div className="seller" onClick={()=>{sellerBuyerSetter(false)}}>{IconPack.checkFalse}I AM A SELLER</div>);

    }else {
        buyerBTN = (<div className="buyer" onClick={()=>{sellerBuyerSetter(true)}}>{IconPack.checkFalse}I AM A BUYER</div>);
        sellerBTN = (<div className="seller active" onClick={()=>{sellerBuyerSetter(false)}}>{IconPack.checkTrue}I AM A SELLER</div>);

    }

    const sellerBuyerSetter = (bool) => {
        setIsBuyer(bool);
        if (bool) {
            setBuyer(props.currentUser.name);
            setSeller('');
        }else {
            setBuyer('');
            setSeller(props.currentUser.name);
        }
    }





        return (
            <Fragment>
                <TabsManager data={tabs} pageTitle={'New deal'}/>
                <div className="dashboard-new-deal dashboard-new-deal--mobile">
                    <div className="selectWho">
                        {buyerBTN}
                        {sellerBTN}
                    </div>

                    <div className="grey-line"></div>
                    <div className="form">
                        <TextField
                            variant="filled"
                            label="Buyer Login"
                            value={buyer}


                        />
                        <TextField
                            variant="filled"
                            label="Seller Login"
                            value={seller}

                        />
                        <TextField
                            variant="filled"
                            label="Amount to send"
                            value={amountToSend}

                        />
                        <TextField
                            variant="filled"
                            disabled
                            label="Comision rate"
                            value={amountComisionRate}

                        />
                        <TextField
                            variant="filled"
                            label="Amount to recive"
                            value={amountToRecive}

                        />
                        <TextField
                            label="Deal title"
                            fullWidth
                            rows={1}
                            value={title}
                            onChange={titleHandler}
                            variant="filled"
                        />
                        <TextField
                            label="Terms of transaction"
                            fullWidth
                            multiline
                            rows={13}
                            value={description}
                            onChange={messHandler}
                            variant="filled"
                        />

                        <div className="terms">
                            <Switch checked={ true }
                                    name="userOptions.seesActivity" />
                            I agree to the rules and accept the terms of the <span className="color-grey">user agreement</span>
                        </div>

                        <div className="btn btn--blue" onClick={() => {
                            createTicket()
                        }}>Create a new deal
                        </div>
                    </div>
                </div>
            </Fragment>
        );





};


const mapStateToProps = (state) => ({
    deals: state.deals.deals,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(NewDeal);