import React, {Fragment} from 'react';
import {connect} from "react-redux";
import TabsManager from './../TabsManager/TabsManager';
import LicenseDashboard from './Dashboard/Dashboard';
import {setCurrentLicense} from '../../../redux/licenses/licenses.actions'
import { FormattedMessage } from 'react-intl';


const Licenses = (props) => {


    return (
        <Fragment>

            <LicenseDashboard tab={props.activeTab}/>

        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCurrentLicense: (license) => dispatch(setCurrentLicense(license))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Licenses);
