import React, {Fragment} from 'react';
import TabsManager from "./../TabsManager/TabsManager";
import './style.scss';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import { IconPack } from './../../../Images/IconPack'
import { FormattedMessage } from 'react-intl';

const Faq = () => {
    let tabs = [
        {name : (<FormattedMessage id='tabsManager.menu.general'/>), link: '/general'},
        {name : (<FormattedMessage id='tabsManager.menu.license'/>), link: '/license'},
        {name : (<FormattedMessage id='tabsManager.menu.payments'/>), link: '/payments'},
        {name : (<FormattedMessage id='tabsManager.menu.problems'/>), link: '/problems'}
    ];


    const [expanded, setExpanded] = React.useState('panel1');

    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };

    return (
        <Fragment>
            <TabsManager data={tabs} pageTitle={'FAQ'}/>
            <div className="dashboard-faq dashboard-faq--mobile">
                <div className="questions">
                    <div>
                        <ExpansionPanel square expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                            <ExpansionPanelSummary aria-controls="panel1d-content" id="panel1d-header">
                                <div className="tab__title">
                                    {expanded === 'panel1' ? IconPack.tabsIcons.questionActive : IconPack.tabsIcons.question}
                                    Are fingerprints saved when transferring sessions to another PC?</div>
                                <div className="tab__btn">
                                    {expanded === 'panel1' ? IconPack.tabsIcons.tabMarkActive : IconPack.tabsIcons.tabMark}
                                </div>
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                                <div className="tab__content">
                                    <div className="greyline"></div>
                                    <p>The fingerprints depend on the hardware  if the hardware is different when transferring, then the final fingerprints will be different. The fingerprints are not included in the config, they are generated when the session is created. Canvas, Audio, clientRects are not included in the config, but generated. <a href="#">WebGL</a>, <a href="">Fonts</a>, <a href="">Plugins</a> are included in the config.
                                    </p>
                                    <b>If you want to have the same fingerprints when transferring, there are 3 options:</b>

                                    <ol>
                                        <li>Use the same virtual machines</li>
                                        <li>In one version, the option to work on the server at the same time was added (there are still some settings with Tor). Accordingly, if the job is on one server via RDP, then the hardware will be the same, as well as the prints during the transfer</li>
                                        <li>A batch of computers with identical hardware. A highly individual option.</li>
                                    </ol>

                                </div>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>

                        <ExpansionPanel square expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                            <ExpansionPanelSummary aria-controls="panel1d-content" id="panel1d-header">
                                <div className="tab__title">
                                    {expanded === 'panel2' ? IconPack.tabsIcons.questionActive : IconPack.tabsIcons.question}
                                    What data does the browser send? How can I verify that you do not steal my data?</div>
                                <div className="tab__btn">
                                    {expanded === 'panel2' ? IconPack.tabsIcons.tabMarkActive : IconPack.tabsIcons.tabMark}
                                </div>
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                                <div className="tab__content">
                                    <div className="greyline"></div>
                                    The fingerprints depend on the hardware  if the hardware is different when transferring, then the final fingerprints will be different. The fingerprints are not included in the config, they are generated when the session is created. Canvas, Audio, clientRects are not included in the config, but generated. WebGL, Fonts, Plugins are included in the config.
                                </div>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>

                        <ExpansionPanel square expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
                            <ExpansionPanelSummary aria-controls="panel1d-content" id="panel1d-header">
                                <div className="tab__title">
                                    {expanded === 'panel3' ? IconPack.tabsIcons.questionActive : IconPack.tabsIcons.question}
                                     If Sox or tunnel does not have its own DNS</div>
                                <div className="tab__btn">
                                    {expanded === 'panel3' ? IconPack.tabsIcons.tabMarkActive : IconPack.tabsIcons.tabMark}
                                </div>
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                                <div className="tab__content">
                                    The fingerprints depend on the hardware  if the hardware is different when transferring, then the final fingerprints will be different. The fingerprints are not included in the config, they are generated when the session is created. Canvas, Audio, clientRects are not included in the config, but generated. WebGL, Fonts, Plugins are included in the config.
                                </div>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>

                        <ExpansionPanel square expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
                            <ExpansionPanelSummary aria-controls="panel1d-content" id="panel1d-header">
                                <div className="tab__title">
                                    {expanded === 'panel4' ? IconPack.tabsIcons.questionActive : IconPack.tabsIcons.question}
                                    How can I use the geolocation function?</div>
                                <div className="tab__btn">
                                    {expanded === 'panel4' ? IconPack.tabsIcons.tabMarkActive : IconPack.tabsIcons.tabMark}
                                </div>
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                                <div className="tab__content">
                                    The fingerprints depend on the hardware  if the hardware is different when transferring, then the final fingerprints will be different. The fingerprints are not included in the config, they are generated when the session is created. Canvas, Audio, clientRects are not included in the config, but generated. WebGL, Fonts, Plugins are included in the config.
                                </div>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>

                    </div>
                </div>
            </div>
        </Fragment>
    );
};

export default Faq;