import { configTypes} from './config.types'

export const setDeviceFilter = type => ({
  type: configTypes.SET_DEVICE_FILTER,
  payload: type
});

export const setOsFilter = type => ({
  type: configTypes.SET_OS_FILTER,
  payload: type
});

export const setOsVersionFilter = (version) => ({
  type: configTypes.SET_OS_VERSION_FILTER,
  payload: version
});

export const setBrowserFilter = type => ({
  type: configTypes.SET_BROWSER_FILTER,
  payload: type
});

export const setBrowserVersionFilter = type => ({
  type: configTypes.SET_BROWSER_VERSION_FILTER,
  payload: type
});


export const updateData = () => ({
  type: configTypes.UPDATE_DATA,

});
export const searchData = value => ({
  type: configTypes.SEARCH_DATA,
  payload: value
});

export const selectItem = value => ({
  type: configTypes.SELECT_ITEM,
  payload: value
});
export const unselectItem = value => ({
  type: configTypes.UNSELECT_ITEM,
  payload: value
});