import React, {Fragment} from 'react';
import './style.scss';
import TabsManager from './../../TabsManager/TabsManager';
import MessageTable from "../Tickets/MessageTable/MessageTable";
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import {setChatState, setChatId} from "../../../../redux/app/app.actions";
import {addMessage, createTicket } from "../../../../redux/support/tickets/tickets.actions";
import {connect} from "react-redux";

import { useHistory } from "react-router-dom";
import { FormattedMessage } from 'react-intl';


const NewTicket = (props) => {
    let tabs = [
        {name: (<FormattedMessage id='tabsManager.menu.tickets'/>), link: '/tickets'},
        {name: (<FormattedMessage id='tabsManager.menu.newTicket'/>), link: '/new-ticket'},
    ];

    const [subject, setSubject] = React.useState('Questions about buying a license');
    const [title, setTitle] = React.useState('Plan to take over the universe');
    const [mess, setMessage] = React.useState('');

    const handleChange1 = (event) => {
        setSubject(event.target.value);
    };

    const handleChange2 = (event) => {
        setTitle(event.target.value);
    };
    const handleChange3 = (event) => {
        setMessage(event.target.value);
    };
    let history = useHistory();

    const createTicket = () => {
        let currentIndex = Date.now();
        if(mess.length !== 0) {
            let ticketObj = {
                title: title,
                subject: subject,
                creationDate: currentIndex,
                data: [{
                    author: props.currentUser.name,
                    owner: true,
                    message: mess,
                    date: currentIndex,
                    dateIndex: currentIndex
                }],
                chatId: currentIndex,
            }

            props.createTicket(ticketObj);
            props.setChatId(currentIndex);
            props.setChatState(true);
            history.push('/tickets');
        }
    }

        return (
            <Fragment>
                <TabsManager data={tabs} pageTitle={'New ticket'}/>
                <div className="dashboard-new-ticket">
                    <div className="form">
                        <TextField
                            variant="filled"
                            fullWidth
                            select
                            label="Subject"
                            value={subject}
                            onChange={handleChange1}
                        >
                            <MenuItem value='NEW Subject'>NEW Subject</MenuItem>
                            <MenuItem value='Questions about buying a license'>Questions about buying a license</MenuItem>
                            <MenuItem value='Questions about buying a license1'>Questions about buying a license1</MenuItem>
                            <MenuItem value='Questions about buying a license2'>Questions about buying a license2</MenuItem>
                            <MenuItem value='Questions about buying a license3'>Questions about buying a license3</MenuItem>
                        </TextField>
                        <TextField
                            variant="filled"
                            fullWidth
                            select
                            label="Title"
                            value={title}
                            onChange={handleChange2}
                        >
                            <MenuItem value={'NEW TITLE'}>NEW TITLE</MenuItem>
                            <MenuItem value={'Plan to take over the universe'}>Plan to take over the universe</MenuItem>
                            <MenuItem value={'Plan to take over the universe1'}>Plan to take over the universe1</MenuItem>
                            <MenuItem value={'Plan to take over the universe2'}>Plan to take over the universe2</MenuItem>
                            <MenuItem value={'Plan to take over the universe3'}>Plan to take over the universe3</MenuItem>
                        </TextField>
                        <TextField
                            label="Message"
                            fullWidth
                            multiline
                            rows={14}
                            value={mess}
                            onChange={handleChange3}
                            variant="filled"
                            className="MessageTextArea"
                        />

                        <div className="btn btn--blue" onClick={() => {
                            createTicket()
                        }}>Create a new ticket
                        </div>
                    </div>
                </div>
            </Fragment>
        );





};


const mapStateToProps = (state) => ({
    tickets: state.tickets.tickets,
    appState: state.appOptions.appState,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChatState: (bool) => dispatch(setChatState(bool)),
        createTicket: (obj) => dispatch(createTicket(obj)),
        setChatId: (numb) => dispatch(setChatId(numb))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(NewTicket);