import React, {Fragment} from 'react';
import {connect} from "react-redux";
import './style.scss';
import MessageComponent from './Message';
import {setChatState} from "../../../../redux/app/app.actions";
import {addMessage} from "../../../../redux/support/tickets/tickets.actions";

import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import { useHistory } from "react-router-dom";



const Chat = (props) => {

    let messageWrapper, currentDataObj;
    let history = useHistory();

    const [mess, setMess] = React.useState('');
    const prepearIndex = (index) => {
        let val = index;
        if(val < 10) {
            val = '0' + val;
        }
        return val;
    }

    const backHandler = () => {
        console.dir(history);
        history.goBack();
        console.dir(history);
    }
    const pushMessage = () => {

        if(mess.length !== 0) {
            let messObj = {
                    chatId: currentDataObj.chatId,
                    message: {
                        author: props.currentUser.name,
                        owner: true,
                        message: mess,
                        date: '15 Feb, 12:21:11',
                        dateIndex: Date.now()
                    }
                };

            props.addMessage(messObj);
            setMess('');
            messObj = null;
        }
    }
    const changeHandler = (target) => {
        setMess(target.value);
    }

    let chatItemsHTML = props.tickets.map((item) => {

        if(item.chatId === props.appState.chatState.currentChatId){
            currentDataObj = item;

            let viewComponent = item.data.map((messageObj, x) => {
                return (<MessageComponent data={messageObj} key={x.toString()}/>);
            });

            return viewComponent;
        }
    })

    let iconPack = {
        send: (<svg width="17" height="17" viewBox="0 0 17 17" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M15.875 0.125L1.375 8.46875C0.8125 8.78125 0.875 9.59375 1.4375 9.8125L4.78125 11.2188L13.75 3.3125C13.9062 3.15625 14.1562 3.375 14 3.5625L6.5 12.7188V15.25C6.5 16 7.375 16.2812 7.8125 15.75L9.8125 13.3125L13.6875 14.9688C14.125 15.1562 14.6562 14.875 14.7188 14.375L16.9688 0.875C17.0938 0.25 16.4062 -0.1875 15.875 0.125Z" fill="#16181C"/>
        </svg>),
        question: (<svg width="14" height="15" viewBox="0 0 14 15" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M7 9.54688C6.5625 9.54688 6.23438 9.90234 6.23438 10.3125C6.23438 10.75 6.5625 11.0781 7 11.0781C7.41016 11.0781 7.76562 10.75 7.76562 10.3125C7.76562 9.90234 7.41016 9.54688 7 9.54688ZM7.19141 8.89062C7.38281 8.89062 7.51953 8.75391 7.51953 8.5625C7.51953 7.38672 9.65234 7.46875 9.65234 5.63672C9.65234 4.24219 8.39453 3.42188 6.97266 3.42188C5.74219 3.42188 5.05859 3.91406 4.48438 4.65234C4.375 4.81641 4.40234 5.00781 4.56641 5.11719L4.92188 5.39062C5.05859 5.47266 5.25 5.44531 5.35938 5.30859C5.76953 4.78906 6.17969 4.51562 6.97266 4.51562C8.06641 4.51562 8.55859 5.08984 8.55859 5.63672C8.55859 6.8125 6.42578 6.64844 6.42578 8.5625C6.42578 8.75391 6.58984 8.89062 6.75391 8.89062H7.19141ZM7 1.34375C10.2266 1.34375 12.9062 3.99609 12.9062 7.25C12.9062 10.5312 10.2539 13.1562 7 13.1562C3.71875 13.1562 1.09375 10.5312 1.09375 7.25C1.09375 3.99609 3.71875 1.34375 7 1.34375ZM7 0.46875C3.25391 0.46875 0.21875 3.53125 0.21875 7.25C0.21875 10.9961 3.25391 14.0312 7 14.0312C10.7188 14.0312 13.7812 10.9961 13.7812 7.25C13.7812 3.53125 10.7188 0.46875 7 0.46875Z" fill="white" fillOpacity="0.6"/>
        </svg>),
        date: (<svg width="13" height="15" viewBox="0 0 13 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M11.3125 2H10V0.578125C10 0.414062 9.83594 0.25 9.67188 0.25H9.45312C9.26172 0.25 9.125 0.414062 9.125 0.578125V2H3.875V0.578125C3.875 0.414062 3.71094 0.25 3.54688 0.25H3.32812C3.13672 0.25 3 0.414062 3 0.578125V2H1.6875C0.949219 2 0.375 2.60156 0.375 3.3125V12.9375C0.375 13.6758 0.949219 14.25 1.6875 14.25H11.3125C12.0234 14.25 12.625 13.6758 12.625 12.9375V3.3125C12.625 2.60156 12.0234 2 11.3125 2ZM1.6875 2.875H11.3125C11.5312 2.875 11.75 3.09375 11.75 3.3125V4.625H1.25V3.3125C1.25 3.09375 1.44141 2.875 1.6875 2.875ZM11.3125 13.375H1.6875C1.44141 13.375 1.25 13.1836 1.25 12.9375V5.5H11.75V12.9375C11.75 13.1836 11.5312 13.375 11.3125 13.375ZM4.42188 9C4.58594 9 4.75 8.86328 4.75 8.67188V7.57812C4.75 7.41406 4.58594 7.25 4.42188 7.25H3.32812C3.13672 7.25 3 7.41406 3 7.57812V8.67188C3 8.86328 3.13672 9 3.32812 9H4.42188ZM7.04688 9C7.21094 9 7.375 8.86328 7.375 8.67188V7.57812C7.375 7.41406 7.21094 7.25 7.04688 7.25H5.95312C5.76172 7.25 5.625 7.41406 5.625 7.57812V8.67188C5.625 8.86328 5.76172 9 5.95312 9H7.04688ZM9.67188 9C9.83594 9 10 8.86328 10 8.67188V7.57812C10 7.41406 9.83594 7.25 9.67188 7.25H8.57812C8.38672 7.25 8.25 7.41406 8.25 7.57812V8.67188C8.25 8.86328 8.38672 9 8.57812 9H9.67188ZM7.04688 11.625C7.21094 11.625 7.375 11.4883 7.375 11.2969V10.2031C7.375 10.0391 7.21094 9.875 7.04688 9.875H5.95312C5.76172 9.875 5.625 10.0391 5.625 10.2031V11.2969C5.625 11.4883 5.76172 11.625 5.95312 11.625H7.04688ZM4.42188 11.625C4.58594 11.625 4.75 11.4883 4.75 11.2969V10.2031C4.75 10.0391 4.58594 9.875 4.42188 9.875H3.32812C3.13672 9.875 3 10.0391 3 10.2031V11.2969C3 11.4883 3.13672 11.625 3.32812 11.625H4.42188ZM9.67188 11.625C9.83594 11.625 10 11.4883 10 11.2969V10.2031C10 10.0391 9.83594 9.875 9.67188 9.875H8.57812C8.38672 9.875 8.25 10.0391 8.25 10.2031V11.2969C8.25 11.4883 8.38672 11.625 8.57812 11.625H9.67188Z" fill="white" fillOpacity="0.6"/>
            </svg>)
    }


    return (
        <Fragment>

            <div className="tabsManager  tabsManager--support">
                <div className="tabsManager__page-title ">
                    <h2>Support</h2>
                </div>
                <div>
                    <div className="back" onClick={()=>{backHandler()}}>
                        Back to tickets
                    </div>

                </div>
            </div>

            <div className="chatWrapper chatWrapper--mobile">
                {chatItemsHTML}
                <div className="anchor"></div>
            </div>

            <div className="chatControls chatControls--mobile">

                <TextField
                    label="Message"
                    value={mess}
                    onChange={(e)=>{
                        changeHandler(e.target);
                    }}
                    multiline
                    rows={4}
                    variant="filled"
                />

                <div className="send btn btn--blue btn--circle" onClick={()=>{pushMessage()}}>{iconPack.send}</div>
            </div>
        </Fragment>
    );
};


const mapStateToProps = (state) => ({
    tickets: state.tickets.tickets,
    appState: state.appOptions.appState,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChatState: (bool) => dispatch(setChatState(bool)),
        addMessage: (obj) => dispatch(addMessage(obj))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Chat);
