import React from 'react';
import {searchData} from "../../../../redux/config-shop/config.actions";
import {connect} from "react-redux";
import FormControl from "@material-ui/core/FormControl";
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import {createItem, setNewValueForStoreItem} from "../../../../redux/licenses/corporate/generate/generate.actions";
import Button from "@material-ui/core/Button";
import { FormattedMessage } from 'react-intl';


const GenerateAlert = (props) => {

    const applyHandler = () => {

    };

    return (<div className="table_action_bar table_action_bar--license table_action_bar--mobile table_action_bar__generate--mobile">
        <div className="mr-10">
            <Button variant="contained" className="btn btn--transparent btn--cancel" >
                <FormattedMessage  id='button.cancel'/>
            </Button>

        </div>
        <div className="ml-10">
            <Button variant="contained" className="btn btn--blue" onClick={() => {
                applyHandler()
            }}><FormattedMessage  id='alertPanel.create'/> {props.generateData.store.length} <FormattedMessage  id='alertPanel.users'/>
            </Button>
        </div>
    </div>);


};



const mapStateToProps = (state) => ({
    generateData: state.corporateLicenseGenerate.generate,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setNewValueForStoreItem: (item, itemValue, id) => dispatch(setNewValueForStoreItem(item, itemValue, id)),
        createItem: (object) => dispatch(createItem(object)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(GenerateAlert);


