import { generateTypes} from './generate.types'


export const setNewValueForStoreItem = ( item, itemValue, id ) => ({
    type: generateTypes.SET_NEW_VALUE_FOR_STORE_ITEM,
    payload: {item, itemValue, id}
})

export const createItem = (object) => ({
    type: generateTypes.CREATE_ITEM,
    payload: object
})

export const deleteItem = (id) => ({
    type: generateTypes.DELETE_ITEM,
    payload: id
})