import React, {Suspense} from 'react';
import './style.scss';
import {setCurrentLicense, setCurrentTab} from "../../../../redux/licenses/licenses.actions";
import {connect} from "react-redux";
import LicenseAlert from './../Alerts/licenseAlert';
import GenerateAlert from './../Alerts/generateAlert';
import InviteAlert from './../Alerts/inviteAlert';
import Preloader from './../../../Preloader/Preloader';
import TabsManager from "../../TabsManager/TabsManager";
import {FormattedMessage} from "react-intl";

const PersonalActivity = React.lazy(() => import('../Activity/PersonalActivity'));
const PersonalCost = React.lazy(() => import('../Cost/PersonalCost'));
const CorporateCost = React.lazy(() => import('./../Cost/CorporateCost'));
const CorporateUsers = React.lazy(() => import('./../Users/CorporateUsers'));
const CorporateActivity = React.lazy(() => import('./../Activity/CorporateActivity'));
const CorporateInvite = React.lazy(() => import('./../Invite/CorporateInvite'));
const CorporateGenerate = React.lazy(() => import('./../Generate/CorporateGenerate'));


const Dashboard = (props) => {

    let activeComponent;
    let alertWrapper = null

    let tabs;

    switch(props.licenses.currentLicense) {
        case 'Personal':
            tabs = [
                {name : (<FormattedMessage id='tabsManager.menu.activity'/>), link: '/licenses/' + props.currentUser.id + '/activity'},
                {name : (<FormattedMessage id='tabsManager.menu.costs'/>), link: '/licenses/' + props.currentUser.id + '/costs'},
            ];
            break;
        case 'Corporate':
            tabs = [
                {name : (<FormattedMessage id='tabsManager.menu.activity'/>), link: '/licenses/' + props.currentUser.id + '/activity'},
                {name : (<FormattedMessage id='tabsManager.menu.users'/>), link: '/licenses/' + props.currentUser.id + '/users'},
                {name : (<FormattedMessage id='tabsManager.menu.costs'/>), link: '/licenses/' + props.currentUser.id + '/costs'},
            ];
            break;
    }

    if(props.licenses.currentLicense == 'Personal') {
        if(props.tab === 'activity'){
            activeComponent = (<PersonalActivity />);
        }else if(props.tab === 'costs') {
            activeComponent = (<PersonalCost />);
        }

    }else if(props.licenses.currentLicense == 'Corporate')  {

        if(props.tab === 'activity'){
            activeComponent = (<CorporateActivity />);
        }else if(props.tab === 'costs') {
            activeComponent = (<CorporateCost />);

        }else if(props.tab === 'users') {
            alertWrapper = <LicenseAlert />;
            activeComponent = (<CorporateUsers />);

            if(props.licenses.currentTab === 'Invite') {
                alertWrapper = <InviteAlert />;
                activeComponent = (<CorporateInvite />);
            } else if(props.licenses.currentTab === 'Generate') {
                alertWrapper = <GenerateAlert />;
                activeComponent = (<CorporateGenerate />);
            }

        }
    }



    return (<>
        <TabsManager data={tabs} license={props.licenses} user={props.currentUser} pageTitle={props.tab}/>
        <div id="licenseDashboard--mobile">

            <Suspense fallback={<Preloader />}> {activeComponent}  </Suspense>
        </div>
            {alertWrapper}
        </>);
};


const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCurrentLicense: (license) => dispatch(setCurrentLicense(license)),
        setCurrentTab : (prop) => dispatch(setCurrentTab(prop))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);