import React, {Fragment} from 'react';
import TabsManager from './../TabsManager/TabsManager';
import DealsAccordion from './DealsAccordion/DealsAccordion';
import { FormattedMessage } from 'react-intl';

const Deals = () => {

    let tabs = [
        {name : (<FormattedMessage id='tabsManager.menu.myDeals'/>), link: '/deals'},
        {name : (<FormattedMessage id='tabsManager.menu.newDeal'/>), link: '/new-deal'}
    ];


    return (
        <>
                <TabsManager data={tabs} pageTitle={'Deals'}/>
                <div className="dashboard-content">
                    <DealsAccordion />
                </div>
        </>
    );
};

export default Deals;